.error-message{
    color: $off-white;
    margin: 0;
    padding-left: 1.5rem;
    font: arial;
}
.add-option-foam{
    display:flex;
    flex-direction: column;
    padding: 2rem;
}
.add-option-input{
    flex-grow: 1;
    background-color:$blue;
    color: $off-white;
    padding: 1rem;
    margin: 0 0 20px 0;
}
@media(min-width:45rem){
    
       .add-option-foam{
           flex-direction: row;
       
    }
    .add-option-input{
        margin: 0 10px  0;
    }
}