.widget-header{
    background-color: $blue;
    display: flex;
    justify-content: space-between;
    padding: 16px;
    color: $off-white;
}
.widget-title{
    margin: 0;
}
.widget{
    background-color: $light-blue;
}
.option-body{
    border-bottom: 1px solid lighten($color: $light-blue, $amount: 10);
    padding: 2rem;
    text-align: center;
    color: $off-white;
}
.option-content{
    border-bottom: 1px solid lighten($color: $light-blue, $amount: 10);
    padding: 2rem;
    display:flex;
    justify-content: space-between;
    color: white;
    font-weight: 500;
}