.big-button{
    background-color: #8357c5;
    color: white;
    border: none;
    border-bottom: .6rem solid darken($color:#8357c5, $amount: 10);
    font-weight: bold;
    font-size: 2.5rem;
    padding: 2.4rem;
    margin-bottom: 10rem;
    width: 100%;
}
.big-button:disabled{
    opacity: .5;
}
button{
    cursor: pointer;
}
button:disabled{
    cursor: default;
}
.button{
    background-color: #8357c5;
    color: white;
    border: none;
    border-bottom: .6rem solid darken($color:#8357c5, $amount: 10);
    font-weight: bold;
    font-size: 1.8rem;
    padding: 1.2rem;  
}
.button-link{
    background-color:$blue;
  color: $off-white;
    border: none;
padding: 0;
     
}