.header{
    background-color: $off-black;
    color: white;
    margin-bottom: $m-size;
    padding: $m-size 0;
    align-content: center;
    justify-content: end;
}
.header_title{
    font-size: $l-size;
    margin: 0;

}

.header_subtitle{
    color: $off-white;
    font-size: $m-size;
    font-weight: 500;
    margin: 0;
}
@media(min-width:45rem){
.header{
    margin-bottom: $xl-size;
}
}